import $ from "cash-dom";
import * as apiFunctions from './api'

const img_form = $('.form_input-img_upload');

if (img_form.length) {
	img_form.find('input[type="file"]').on('change', function(){
		if (this.files.length) {
			const img_src = window.URL.createObjectURL(this.files[0]);
			img_form.find('img').attr('src', img_src);
		}
	})
	img_form.find('.change-file').on('click', () => img_form.find('input[type="file"]').trigger('click'))
}

const submit_button = $('.submit-button');

if (submit_button.length) {
	submit_button.on('click', function(e){
		e.preventDefault();
		const functionApi = $(this).data('func');
		if (!!functionApi && apiFunctions[functionApi]) {
			apiFunctions[functionApi](submit_button.parents('.form-setting'));
		}
	})
}


export const bootBanner = (text, className = '') => {
	$('.banner-status').remove();
	const banner = $(`<div class="banner-status ${className}"><div class="banner-status-text">${text}</div><span class="close-banner"></span></div>`);
	$('.header').before(banner);
	banner.find('.close-banner').on('click', function(){
		banner.removeClass('active');
		setTimeout(() => banner.remove(), 500);
	})
	setTimeout(() => banner.addClass('active'), 100)
}
