import $ from "cash-dom";
import { getTags, uploadImage } from "./api";
import "../lib/tagify";

const upload_list = $(".upload-form-list");
if (upload_list.length) {
	const button_upload = $('.upload-system-droping');
	const template = $('#upload-item');
	const fileInput = button_upload.siblings('.file-input');
	const load_all_container = $('.load-all-container');


	let prepareUploadData = [];

	fileInput.on('change', function(){
		if (this.files.length) {
			addUploadItem(this.files)
		}
	})



	button_upload.on('click', () => fileInput.trigger('click'))


	$('body').on('drag dragstart dragend dragover dragenter dragleave drop', function(e) {
		e.preventDefault();
		e.stopPropagation();
	})
	.on('dragover dragenter', function() {
		button_upload.addClass('is-dragover');
	})
	.on('dragleave dragend drop', function() {
		button_upload.removeClass('is-dragover');
	})
	.on('drop', function(e) {
		addUploadItem(e.dataTransfer.files)
	});

	upload_list.on('click', '.additional-container-helper', function(){
		$(this).toggleClass('active').siblings('.hidden-content').toggleClass('active-content');
	})
	upload_list.on('click', '.delete-image', function(e){
		e.preventDefault();
		
		const parent = $(this).parent();
		prepareUploadData = prepareUploadData.filter((item) => !item.element[0].isEqualNode(parent[0]));
		deleteItem($(this).parent());
		updateCountImages();
	})

	load_all_container.find('.load-all').on('click', () => {
		load_all_container.removeClass('active-load');
		if (prepareUploadData.length) {
			prepareUploadData.forEach((item) => {
				uploadImageAction(item.element);
			})
		}
	})

	const tagifyInput = (el) => {

		const input = el.find('.tagify-js')[0];
		
		const tagify = new Tagify(input, {
			originalInputValueFormat: valueArr => valueArr.map(item => item.value).join(',')
		});

		let timer;

		tagify.on('input', function(e){
			tagify.whitelist = null;
			tagify.loading(true);
			if (timer !== undefined) {
				clearInterval(timer);
			}
			timer = setTimeout(() => {
				return getTags(
					e.detail.value,
					function(res){
						tagify.settings.whitelist = res.data;
						tagify.loading(false).dropdown.show(e.detail.value);
					}
				)
			}, 200)
			
		})
	}

	const updateCountImages = () => {
		load_all_container.find('.load-all-container_text_count').text(prepareUploadData.length);
		if (prepareUploadData.length < 1) {
			load_all_container.removeClass('active-load');
		}else{
			load_all_container.addClass('active-load');
		}
	}

	const deleteItem = (el) => {
		setTimeout(() => el.remove(), 1000);
		el.addClass('remove-item');
	}

	const addErrorUpload = (el, error = '') => {
		setLoadingProgress(el, 0)
		el.addClass('is-error').find('.error-image span').html(error);
		updateCountImages();
	} 

	const addSuccess = (el, res) => {
		prepareUploadData = prepareUploadData.filter((item) => !item.element[0].isEqualNode(el[0]));
		el.addClass('is-success');
		updateCountImages();
	}

	const setLoadingProgress = (element, width) => {
		element.find('.loaded-progress').css('width', `${width}%`);
	}

	const uploadImageAction = (el) => {
		el.removeClass('is-error');

		const {element, file} = prepareUploadData.find((item) => item.element[0].isEqualNode(el[0]));
		

		uploadImage(element, file, addErrorUpload, addSuccess, {
			onUploadProgress: progressEvent => setLoadingProgress(element, (progressEvent.total / progressEvent.loaded) * 100)
		});
	}

	let uploadId = 0;

	const addUploadItem = (files) => {
		Array.from(files).map((file) => {
			const img_src = window.URL.createObjectURL(file);
			const img = new Image();
			img.src = img_src;
			img.alt = file.name;
			$(img).addClass('upload-img');
			const clone = template[0].content.cloneNode(true);
			const id = `upload-${uploadId}`;
			img.onload = function(){
				const lastItem = $(`#${id}`);
				lastItem.find('.info-file_pixels').text(`${this.naturalWidth}x${this.naturalHeight}`);
				tagifyInput(lastItem);
				lastItem.find('.upload-img').on('click', function(){
					uploadImageAction(lastItem);
				})
				prepareUploadData.push({
					element: lastItem,
					file
				})
				updateCountImages();
			}
			

			$(clone).find('.info-file_name').text(file.name);
			$(clone).find('.info-file_size').text(formatBytes(file.size));
			$(clone).find('.upload-form-list__item').attr('id', id);

			$(clone).find('.upload-img_container .error-image').before(img);
			upload_list.append($(clone));
			uploadId++;
		})
		
		
	}
}


function formatBytes(bytes, decimals = 2) {
    if (bytes === 0) return '0 Bytes';

    const k = 1024;
    const dm = decimals < 0 ? 0 : decimals;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];

    const i = Math.floor(Math.log(bytes) / Math.log(k));

    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
}