import axios from "axios";
import $ from "cash-dom";
import { bootBanner } from "./index";

window.t = $;

if (!window.page) {
	window.page = {
		locale: 'en'
	}
}

function addErrorInput(el, text){
	el.after(`<span class="error-input">${text}</span>`);
}

export const uploadImage = (el, file, errorCallback = false, callback = false, config = {}) => {

	const formData = new FormData();

	formData.append('file', file);

	el.find('input').each(function(){
		console.log($(this).attr("name"), $(this).val());
		formData.append($(this).attr("name"), $(this).val())
	})

	

	axios.post(`/api/user/upload?locale=${window.page.locale}`, formData, config)
	.then(function({data}) {
		if (data['errors']) {
			if (!!errorCallback) {
				errorCallback(el, data['errors'][0]);
			}
		}else{
			if(!!callback){
				setTimeout(() => {
					callback(el, data);
				}, 500);
				
			}
		}
		
	})
	.catch(err => console.log(err))
}

export const getTags = (tag, callback) => {
	axios.get(`/api/tag_hint/${window.page.locale}/${tag}`)
	.then(function({data}){
		callback(data);
	})
}

export const setAccountSettings = (form) => {
	const dataReq = form.serialize();
	form.find('.error-input').remove();
	axios.post(`/api/account/settings?locale=${window.page.locale}`, dataReq)
	.then(function({data}){
		if (data['error']) {
			for (const key in data['error']) {
				const input = form.find(`[name="${key}"]`);
				if (input.length) {
					addErrorInput(input, data['error'][key]);
				}
			}
		}

		if (data['success']) {
			bootBanner(data['success']);
		}
	})
}